<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">{{ $t('GinTermsConditions') }}:
                            {{ ItemData.Name }}
                        </h5>
                        <button @click="CloseAcceptTermsConditions" type="button" class="close"
                            data-original-title="Close"><em class="fa fa-times"></em>
                        </button>
                    </div>
                    <div class="border p-3">
                    <div class="row">
                        <div class="modal-body w-100 py-0">
                            <section class="tab mt-2">
                                <div class="tab-style">
                                    <div class="tab_container mb-0">
                                        <div class="responsive-tabs-container accordion-xs accordion-sm">
                                            <div class="listing tab-content">
                                                <div id="" class="">
                                                    <div class="container-fluid p-0">
                                                        <div class="row">
                                                            <div class="col-lg-6 mb-2">
                                                                <div class="p-2 bg-grey text-center"><b class="font-weight-medium">{{ $t('CRM_PRODUCTS') }}: </b> <span class="detailtxt"><lable id="lbl-catalogName" />{{ ItemData.AssetCategoryName }}</span></div>
                                                            </div>
                                                            <div class="col-lg-6 mb-2">
                                                                <div class="p-2 bg-grey text-center"><b class="font-weight-medium">{{$t('UniqueName')}}: </b> <span class="detailtxt"><lable id="lbl-uniqeName" /> {{ ItemData.Name }}</span></div>
                                                            </div>
                                                            <div class="col-lg-6 mb-2">
                                                                <div class="p-2 bg-grey text-center"><b class="font-weight-medium">{{ $t('AssignedFrom') }}: </b><span class="detailtxt"><lable id="lbl-assignedFrom" />{{ ItemData.AssignedFrom }}</span></div>
                                                            </div>
                                                            <div class="col-lg-6 mb-2">
                                                                <div class="p-2 bg-grey text-center"><b class="font-weight-medium">{{ $t('AssignedTill') }}: </b><span class="detailtxt"><lable id="lbl-assignedTill" />{{ ItemData.AssignedTill }}</span> </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="mt-2 row">
                                                    <Form class="w-100">
                                                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                                                            <div class="table-responsive">
                                                                <table class="table table-bordered" id="tblRecord">
                                                                    <thead class="thead-bg">
                                                                        <tr>
                                                                            <th class="text-center">{{ $t('AttachmentName') }}</th>
                                                                            <th class="text-center">{{$t('FileName')}}</th>
                                                                            <th class="text-center">{{ $t('Actions') }}</th>
                                                                        </tr>
                                                                    </thead>
                                                                    <tbody>
                                                                        <tr v-if="noRecords">
                                                                            <td class="text-center tdcm arial12ff0000 text-danger" colspan="3">
                                                                                <span>{{$t('FileNotFoundForDownload')}}</span>
                                                                            </td>
                                                                        </tr> 
                                                                        <tr v-for="(item,index) in AttachmentData" :key="index">
                                                                            <td class="text-center">
                                                                                {{item.ATTACHMENT_TITLE}}
                                                                            </td>
                                                                            <td class="text-center">
                                                                                {{item.ATTACHMENT_NAME}}
                                                                            </td>
                                                                            <td class="text-center">
                                                                                <a href="javascript:;" title="DownloadFile" alt="DownloadFile" class="downloadfile" @click="Downloadfile" :value="item.ASSET_ATTACHMENT_ID" :fileName="item.ATTACHMENT_NAME" :filePath="item.ATTACHMENT_PATH" :fileType="item.ATTACHMENT_TYPE">
                                                                                    <em class="fa fa-download action-icon text-success"></em>
                                                                                </a>
                                                                            </td>
                                                                        </tr> 
                                                                    </tbody>
                                                                </table>
                                                            </div>
                                                        </div>
                                                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-2 float-left">
                                                            <div class="form-group">
                                                                <div class="custom-control custom-checkbox" v-if="showCheckBox">
                                                                    <input type="checkbox" id="chk_terms" class="chkItems chktermcondition" v-on:click="checkCheckbox($event)"/>
                                                                    <label>{{ $t('AgreeToTermsConditions') }}</label>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                                            <div class="form-group float-md-right mb-0">
                                                                <div class="search-btm-btn">                                                                   
                                                                        <a v-if="showCheckBox" @click="SaveTerms" class="btn btn-success formbtn text-white mr-2" :class="{'disabled':IscheckCheckbox==false}" :disabled="{'disabled':IscheckCheckbox==false}" title="Save" style="cursor: pointer;" id="btnSaveTerms"><em class="fa fa-save pr-2"></em>{{ $t('Save') }}</a>
                                                                        <a v-else class="btn btn-success formbtn disabled text-white mr-2" title="Save" style="cursor: pointer;" id="btnSaveTerms" disabled="disabled"><em class="fa fa-close pr-2"></em>{{ $t('Save') }}</a>
                                                                        <a class="btn btn-danger formbtn text-white" title="Cancel" style="cursor: pointer;" @click="CloseAcceptTermsConditions"><em class="fa fa-close pr-2"></em>{{ $t('Cancel') }}</a>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </Form>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </section>
                        </div>
                    </div>
                    </div>
                </div>
            </div>
        </div>
    </div>   
</template>

<script>
import DataService from '../../service/DataService';
export default {
    props:{
        ItemData:{
            type:Array,
            required:true
        }
    },
    data(){
        return{
            noRecords:false,
            showCheckBox:false,
            AttachmentData:[],
            IscheckCheckbox:false,
            isLoading:false
        }
    },
    created:function(){        
        this.GetTermsConditionsFilePath();
    },
    methods:{
        GetTermsConditionsFilePath:function(){
            var vm=this;            
            var url=`assetCatalogId=${vm.ItemData.AssetCatalogueId}`;
            DataService.GetTermsConditionsFilePath(url).then(response=>{                                
                if(response.data.length>0){                    
                    vm.AttachmentData=response.data;
                    vm.showCheckBox=vm.AttachmentData[0].ATTACHMENT_PATH==''?false:true;                    
                }
                else{
                    vm.noRecords=true;
                    vm.AttachmentData=[];
                }
                setTimeout(function(){vm.CheckBoxBootstrap()}, 100);   
            });
        },
        checkCheckbox:function(event){            
            if(event.target.checked){
                this.IscheckCheckbox=true;
            }else{
                this.IscheckCheckbox=false;
            }
        },
        CloseAcceptTermsConditions:function(){        
            this.$emit('CloseAcceptTermsConditions');
        },
        SaveTerms:function(){            
            var vm=this;
            vm.isLoading=true;
            let obj={
                related_info_id:this.ItemData.RelatedInfoId,
                asset_catalog_id:this.ItemData.AssetCatalogueId
            }
            var JsonObj=JSON.stringify(obj);
            DataService.SaveTermsConditions(JsonObj).then(response=>{                
                if(response.data.status==0){
                    vm.ShowAlert(response.data.msg, "success", true, vm.$t('Alert'));
                    vm.CloseAcceptTermsConditions();
                    vm.$parent.FetchData();
                }
                else{
                    vm.ShowAlert(response.data.msg, "failure", true, vm.$t('Alert'));
                }
                vm.isLoading=false;
            });
        },
        Downloadfile:function(){  
            var vm=this;          
            vm.isLoading=true;
            var url=`fileId=${this.AttachmentData[0].ASSET_ATTACHMENT_ID}&filePath=${this.AttachmentData[0].ATTACHMENT_PATH}&fileName=${this.AttachmentData[0].ATTACHMENT_NAME}&fileType=${this.AttachmentData[0].ATTACHMENT_TYPE}`;
            DataService.Downloadfile(url).then(response=>{
                vm.isLoading=false;
                var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                var fileLink = document.createElement('a');
                fileLink.href = fileURL;
                fileLink.setAttribute('download', (this.AttachmentData[0].ATTACHMENT_NAME));
                document.body.appendChild(fileLink);
                fileLink.click();
            });
        }
    }
}
</script>